import React from "react";
import FormErrors from "../../common/components/FormErrors";
import FormStyleProvider from "./FormStyleProvider";

const Form1 = ({ register, errors }) => {
  return (
    <FormStyleProvider>
      <div className="step__form--general page--1 w-100 d-flex flex-column flex-lg-row">
        <div className="step__form--general--left">
          <p className="fs__title--main fw-bolder">Nhập thông tin</p>
          <div className="step__form__input">
            <input
              type="text"
              name="fullname"
              placeholder="Họ và tên đầy đủ"
              ref={register({ required: "Cần nhập đầy đủ thông tin" })}
            />
            <input
              type="date"
              name="birth"
              ref={register({ required: "Cần nhập đầy đủ thông tin" })}
            />
          </div>
          <FormErrors errors={errors} />
        </div>
        <div className="step__form--general--right">
          <p className="fs__title--white">Xin chào</p>
          <p className="fs__text">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Exercitationem ad porro
            perferendis quo error. Qui esse adipisci natus, ullam deserunt aliquam, itaque magni
            dolor debitis voluptatibus corporis minus corrupti eveniet!
          </p>
          <p className="fs__details">
            Lưu ý: Lorem ipsum dolor, sit amet consectetur adipisicing elit. Possimus itaque sint
            nulla asperiores laudantium molestiae.
          </p>
        </div>
      </div>
    </FormStyleProvider>
  );
};

export default Form1;
