import React, { useState } from "react";
import { useForm } from "react-hook-form";
import styled from "styled-components";
import useActiveCode from "../../common/hooks/useActiveCode";
import Form1 from "./Form1";
import Form2 from "./Form2";
import Form3 from "./Form3";
import Stepper from "./Stepper";
import { config } from "../../config/api";
import useAxios from "../../common/hooks/useAxios";
import { useHistory } from "react-router";

const GettingReport = ({ className }) => {
  const history = useHistory();
  const { mutate: activeCode, error: activeCodeError } = useActiveCode();
  const { register, handleSubmit, errors } = useForm();
  const [numbers, setNumbers] = useState([]);
  const axios = useAxios();
  const [step, setStep] = useState(0);
  const form = [
    <Form1 register={register} errors={errors} />,
    <Form2 numbers={numbers} />,
    <Form3 register={register} errors={errors} activeCodeError={activeCodeError} />,
  ];
  const getForm = (step) => {
    return form[step];
  };
  const handleMultiform = async (data) => {
    try {
      switch (step) {
        case 0:
          // console.log(data);
          const { data: formData } = await axios.post(
            `${config.apiGateWay}/dashboard/step-2`,
            data
          );
          // console.log(formData);
          setNumbers(formData?.rs);
          break;
        case 2:
          activeCode(data.code, {
            onSuccess: () => {
              history.push("/user/pdf");
            },
          });
          break;
        default:
      }
      next();
    } catch (error) {}
  };

  const next = () => {
    if (step < form.length - 1) setStep((prev) => ++prev);
  };
  return (
    <section className={`${className} container step_by_step`}>
      <form onSubmit={handleSubmit(handleMultiform)}>
        <main className="step__form my-3">
          <Stepper step={step} />
          {getForm(step)}
          <section className="step__form__button mt-3">
            <button
              type="button"
              className={`prev step__from__button button-secondary--outline ${
                step <= 0 ? "button-disable" : ""
              }`}
              onClick={() => setStep((prev) => Math.max(0, prev - 1))}
            >
              <span style={{ marginRight: 10 }}>
                <i className="fas fa-long-arrow-alt-left"></i>
              </span>
              Trở về
            </button>
            <button
              type="submit"
              className="next step__from__button button-secondary"
              // onClick={step < form.length - 1 ? next : null}
            >
              {step === 2 ? "Hoàn thành" : "Tiếp theo"}
              {step < 2 && (
                <span style={{ marginLeft: 10 }}>
                  <i className="fas fa-long-arrow-alt-right"></i>
                </span>
              )}
            </button>
          </section>
        </main>
      </form>
    </section>
  );
};

export default styled(GettingReport)`
  .step__form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &__button {
      -webkit-touch-callout: none; /* iOS Safari */
      -webkit-user-select: none; /* Safari */
      -khtml-user-select: none; /* Konqueror HTML */
      -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
      user-select: none;
      display: flex;
      justify-content: space-between;
      > button {
        padding: 10px 15px;
        border: 0;
        font-weight: bolder;
        &.button-disable {
          visibility: hidden;
        }
        /* &:nth-child(1) {
          background-color: rgba(255, 255, 255, 0);
          &:hover {
            background-color: rgba(255, 255, 255, 0.385);
          }
        }
        &:nth-child(2) {
          color: #000;
          &:hover {
            background-color: #ffc267d7;
          }
        } */
      }
    }
  }
`;
