import React from "react";
import styled from "styled-components";

const NumberItem = ({ className, name, value, ...rest }) => {
  return (
    <div className={`${className} number-item mb-2`} {...rest}>
      <h4>Chỉ số {name}</h4>
    </div>
  );
};

export default styled(NumberItem)`
  &.number-item {
    border-radius: 4px;
    background-color: #d4d4d4;
    h4 {
      text-transform: uppercase;
      padding: 20px;
      margin-bottom: 0;
      font-size: 1rem;
      font-weight: 700;
      position: relative;
      cursor: ${(props) => (props.value ? "pointer" : "initial")};
      &::after {
        position: absolute;
        font-family: ${(props) => (props.value ? "inherit" : '"Font Awesome 5 Free"')};
        -webkit-font-smoothing: antialiased;
        display: inline-block;
        font-style: normal;
        font-variant: normal;
        text-rendering: auto;
        line-height: 1;
        font-weight: 900;
        content: "${(props) => (props.value ? props.value : "\f023")}";
        right: 20px;
        transform: rotate(0);
        transform-origin: center;
        transition: all 0.2s ease-in-out;
      }
    }
    .content {
      padding: 0 20px 20px;
    }
  }
`;
